const MAX_ITERATIONS = 100;
const THRESHOLD = 1000000;
const colormap = ["rgb(255, 0, 0)", "rgb(255, 255, 0)", "rgb(0, 255, 0)", "rgb(0, 255, 255)", "rgb(0, 0, 255)", "rgb(255, 0, 255)", "rgb(0, 0, 0)", "rgb(0, 0, 0)", "rgb(0, 0, 0)", "rgb(0, 0, 0)"];


function complexSum(a, b) {
    return [a[0] + b[0], a[1] + b[1]];
}


function mandelbrotIterations(c) {
    let z = [0, 0];
    for (let i = 1; i < MAX_ITERATIONS; i += 1) {
        z = complexSum([z[0] * z[0] - z[1] * z[1], 2 * z[0] * z[1]], c);
        if ((z[0] * z[0] + z[1] * z[1]) > THRESHOLD) {
            return i;
        }
    }
    return 0;
}

function visualize(center) {
    const canvas = document.querySelector(".visualizer");
    const WIDTH = canvas.width;
    const HEIGHT = canvas.height;

    const canvasCtx = canvas.getContext("2d");
    canvasCtx.clearRect(0, 0, WIDTH, HEIGHT);
    let zoom = 0.1;
    let drawVisual;

    const draw = () => {
        drawVisual = requestAnimationFrame(draw);


        for (let i = 0; i < WIDTH; i += 1) {
            for (let j = 0; j < HEIGHT; j += 1) {

                const c = [center[0] + zoom * (i - WIDTH/2), center[1] + zoom * (j - HEIGHT/2)];
                const value = mandelbrotIterations(c);

                if (value == 0) {
                    canvasCtx.fillStyle =  "rgb(0, 0, 0)";
                } else {
                    canvasCtx.fillStyle = colormap[value % colormap.length];
                }
                canvasCtx.fillRect(i, j, i+1, j+1);
            }
        }

        zoom *= 0.95;

        if (zoom < 0.001) {
            zoom = 0.1;
        }
    };

    draw();
}

function init(c_real, c_im) {
    visualize([null, null]);
}

export { init };