let audioCtx = new AudioContext();
const ctx = {
    source: null,
    newSample: null,
    canvas: null,
    analyser: null,
    distortion: null,
    gainNode: null,
    biquadFilter: null,
    convolver: null
};

// Distortion curve for the waveshaper, thanks to Kevin Ennis
// http://stackoverflow.com/questions/22312841/waveshaper-node-in-webaudio-how-to-emulate-distortion
function makeDistortionCurve(amount) {
    let k = typeof amount === "number" ? amount : 50,
        n_samples = 44100,
        curve = new Float32Array(n_samples),
        deg = Math.PI / 180,
        i = 0,
        x;
    for (; i < n_samples; ++i) {
        x = (i * 2) / n_samples - 1;
        curve[i] = ((3 + k) * x * 20 * deg) / (Math.PI + k * Math.abs(x));
    }
    return curve;
}

function visualize() {
    const WIDTH = ctx.canvas.width;
    const HEIGHT = ctx.canvas.height;

    ctx.analyser.fftSize = 256;
    const bufferLength = ctx.analyser.frequencyBinCount;
    console.log(bufferLength);

    // See comment above for Float32Array()
    const dataArray = new Uint8Array(bufferLength);

    const canvasCtx = ctx.canvas.getContext("2d");
    canvasCtx.clearRect(0, 0, WIDTH, HEIGHT);
    let drawVisual;

    const draw = () => {
        drawVisual = requestAnimationFrame(draw);

        ctx.analyser.getByteFrequencyData(dataArray);

        canvasCtx.fillStyle = "rgb(0, 0, 0)";
        canvasCtx.fillRect(0, 0, WIDTH, HEIGHT);

        const barWidth = (WIDTH / bufferLength) * 2.5;
        let x = 0;

        for (let i = 0; i < bufferLength; i++) {
            const barHeight = dataArray[i];

            canvasCtx.fillStyle = "rgb(" + (barHeight + 100) + ",50,50)";
            canvasCtx.fillRect(
                x,
                HEIGHT - barHeight / 2,
                barWidth,
                barHeight / 2
            );

            x += barWidth + 1;
        }
    };

    draw();

}

function voiceChange() {
    ctx.distortion.oversample = "4x";
    ctx.biquadFilter.gain.setTargetAtTime(0, audioCtx.currentTime, 0);
}

async function init(audioData, callback) {
    // Set up the different audio nodes we will use for the app
    ctx.newSample = false;
    ctx.analyser = audioCtx.createAnalyser();
    ctx.analyser.minDecibels = -90;
    ctx.analyser.maxDecibels = -10;
    ctx.analyser.smoothingTimeConstant = 0.85;

    ctx.distortion = audioCtx.createWaveShaper();
    ctx.gainNode = audioCtx.createGain();
    ctx.biquadFilter = audioCtx.createBiquadFilter();
    ctx.convolver = audioCtx.createConvolver();

    // Grab audio track via fetch() for convolver node
    try {
        const response = await fetch(
            "https://mdn.github.io/voice-change-o-matic/audio/concert-crowd.ogg"
        );
        const arrayBuffer = await response.arrayBuffer();
        const decodedAudio = await audioCtx.decodeAudioData(arrayBuffer);
        ctx.convolver.buffer = decodedAudio;
    } catch (error) {
        console.error(
            `Unable to fetch the audio file: Error: ${error.message}`
        );
    }

    // Set up canvas context for visualizer
    ctx.canvas = document.querySelector(".visualizer");

    const intendedWidth = document.querySelector(".wrapper").clientWidth;
    ctx.canvas.setAttribute("width", intendedWidth);
    const audioDataCopy = new ArrayBuffer(audioData.slice(0))
    

    audioCtx.decodeAudioData(audioData).then(buffer => {
        ctx.source = audioCtx.createBufferSource();
        ctx.source.buffer = buffer;
        ctx.newSample = true;
        ctx.source.connect(ctx.distortion);
        ctx.distortion.connect(ctx.biquadFilter);
        ctx.biquadFilter.connect(ctx.gainNode);
        ctx.convolver.connect(ctx.gainNode);
        ctx.gainNode.connect(ctx.analyser);
        ctx.analyser.connect(audioCtx.destination);
        ctx.source.onended = () => { 
            callback(); 
            init(audioDataCopy, callback);
        };

        visualize();
        voiceChange();
    }).catch(function (err) {
        console.error("The following gUM error occured: " + err);
    });    

}

function playSample() {
    if (ctx.newSample) {
        ctx.source.start();
        ctx.newSample = false;
    }
    audioCtx.resume();
}

function stopSample() {
    audioCtx.suspend();
}

function updateGain(gain) {
    if (ctx.gainNode) { // wait for node to be initalized
        ctx.gainNode.gain.value = gain;
    }
}

function updateDistortion(distortion) {
    if (ctx.distortion) { // wait for node to be initalized
        ctx.distortion.curve = makeDistortionCurve(distortion);
    }
}

function updateType(type) {
    if (ctx.convolver && ctx.biquadFilter) { // wait for nodes to be initalized
        voiceChange();

        if (type == 0) {
            // No type
            ctx.biquadFilter.disconnect(0);
            ctx.biquadFilter.connect(ctx.gainNode);
        } else if (type == 1) {
            ctx.biquadFilter.disconnect(0);
            ctx.biquadFilter.connect(ctx.convolver);
        } else if (type == 2) {
            ctx.biquadFilter.disconnect(0);
            ctx.biquadFilter.connect(ctx.gainNode);
            ctx.biquadFilter.type = "lowshelf";
            ctx.biquadFilter.frequency.setTargetAtTime(1000, audioCtx.currentTime, 0);
            ctx.biquadFilter.gain.setTargetAtTime(25, audioCtx.currentTime, 0);
        }
    }
}


export { init, playSample, stopSample, updateGain, updateDistortion, updateType };