import { useState, useEffect } from "react";
import { init, playSample, stopSample, updateGain, updateDistortion, updateType } from "../../model/fourier";
import classes from "./fourier.css";

const reader = new FileReader();


function Fourier() {
    const [uploadedAudio, setUploadedAudio] = useState(null);
    const [isPlaying, setIsPlaying] = useState(false);
    const [gain, setGain] = useState(null);
    const [distortion, setDistortion] = useState(null);
    const [type, setType] = useState(null);


    // AUDIO BUFFER
    useEffect(() => {
        if (uploadedAudio) {
            reader.readAsArrayBuffer(uploadedAudio.target.files[0]);
            reader.onload = function (e) {
                const playback = () => { stop(); }; // Allow for playback when song finishes
                init(e.target.result, playback);
                setIsPlaying(false);
            };   
        }
    }, [uploadedAudio]);


    useEffect(() => {
        if (gain) {
            updateGain(gain.target.value);
        }
    }, [gain]);

    useEffect(() => {
        if (distortion) {
            updateDistortion(distortion.target.value);
        }
    }, [distortion]);

    useEffect(() => {
        if (type) {
            updateType(type.target.value);
        }
    }, [type]);

    function play() {
        playSample();
        setIsPlaying(true);
    }

    function stop() {
        stopSample();
        setIsPlaying(false);
    }


    return (
        <div className="fourier">
            <div className="file-upload">
                <label>Provide an audio file</label>
                <br />
                <input type="file" accept=".mp4,.m4a,.mp3" onChange={setUploadedAudio} />
            </div>
            <div className="frequencies wrapper">
                <canvas className="visualizer" width="640" height="100"></canvas>
            </div>
            <div className="controls">
                <label>Gain: </label><input type="range" min="0" max="5" step="0.5" defaultValue="1" className="slider" id="gain" onChange={setGain} /><br />
                <label>Distortion: </label><input type="range" min="0" max="800" step="100" defaultValue="0" className="slider" id="distortion" onChange={setDistortion} /><br />
                <label>None - Convolver - Biquad: </label><input type="range" min="0" max="2" defaultValue="0" className="slider" id="type" onChange={setType} /><br />


                <button onClick={play} disabled={isPlaying}>Play</button>
                <button onClick={stop} disabled={!isPlaying}>Pause</button>
            </div>
            <p>Some code stolen from: <a href="https://github.com/mark-mxwl/freqy">https://github.com/mark-mxwl/freqy</a></p>
            <p>And from: <a href="https://github.com/mdn/webaudio-examples">https://github.com/mdn/webaudio-examples</a></p>
        </div>
    );
}

export default Fourier;