import { useState } from "react";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { useParallax } from 'react-scroll-parallax';
import classes from './swift.css';

function Swift() {

    let [song, setSong] = useState('');
    const [era, setEra] = useState('');
    const { ref } = useParallax({ speed: 10 });

    const eraToSong = {
        "angry": <iframe ref={ref} src="https://www.youtube.com/embed/Qf9SOEkZR9Q?list=PLbf3ayzuvZVU9NvZVGv4EE_YLlli0IEpt" title="... Ready For It?" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>,
        "mellow": <iframe ref={ref} src="https://www.youtube.com/embed/EXLgZZE072g" title="Taylor Swift - evermore (Official Lyric Video) ft. Bon Iver" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
    }

    function handleSetEra(event) {
        setEra(event.target.value);   
        setSong(eraToSong[event.target.value]);
    }
    

    return (
        <div className="swift">
            <h1 className="title">Way to Improve your Mood</h1>
            <div className="solution">
                <h2 ref={ref}>1. The transition from <span className="song-title">Don't Blame Me</span> to <span className="song-title">Look What You Made Me Do</span></h2>
                <iframe src="https://www.youtube.com/embed/agMPE6oZEEA" title="Taylor Swift: The Eras Tour - Don&#39;t Blame Me/Look What You Made Me Do (Studio Version Audio)" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
            </div>
            <div className="solution">
                <h2>2. The bridge of <span className="song-title">Cruel Summer</span></h2>
                <iframe ref={ref} src="https://www.youtube.com/embed/ic8j13piAhQ" title="Taylor Swift - Cruel Summer (Official Audio)" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
            </div>
            <div className="solution">
                <h2>3. Find your Era</h2>
                <div>
                    <FormControl sx={{ m: 1, minWidth: 120, maxWidth: 240 }}>
                        <InputLabel id="era-input">Era</InputLabel>
                        <Select
                            labelId="era-select"
                            id="era-select"
                            value={era}
                            label="Era"
                            onChange={handleSetEra}
                        >
                            <MenuItem value={"angry"}>Angry</MenuItem>
                            <MenuItem value={"mellow"}>Mellow / Sad</MenuItem>
                        </Select>
                    </FormControl>
                </div>
                { song }
            </div>
            <div className="solution">
                <h2>4. 10 Minute Therapy is Expensive, Try this Instead</h2>
                <iframe ref={ref} src="https://www.youtube.com/embed/aPnqx56V8-0" title="All Too Well (10 Minute Version) (Taylor&#39;s Version) (From The Vault)" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
            </div>
        </div>
    );
}

export default Swift;