import { useState } from "react";
import { init } from "../../model/mandelbrot";

function Mandelbrot() {

    function generateMandelbrot(e) {
        // Prevent browser from reloading the page
        e.preventDefault();

        // Read the form data
        const form = e.target;
        const formData = new FormData(form);
        const c_real = formData.get("c_read");
        const c_im = formData.get("c_im");
        init(c_real, c_im);
    }

    return (
        <div className="mandelbrot">
            <div className="wrapper">
                <canvas className="visualizer" width="640" height="300"></canvas>
            </div>
            <form method="post" onSubmit={generateMandelbrot}>
                {/* <label>Real(C) </label><input type="number" name="c_read" step="any" /> <br />
                <label>Imaginary(C) </label><input type="number" name="c_im" step="any" /> <br /> */}
                <button type="submit">Submit</button>
            </form>
            {/* <div className="points">
                <h3>Interesting points</h3>
                <ul>
                    <li>0, 0</li>
                </ul>
            </div> */}
            
        </div>
    );
}

export default Mandelbrot;